import React from "react";

const Loading = () => {
  return (
    <div className="loading-background flex items-center justify-center min-h-screen">
      <div className="relative">
        <div
          className="spinner-border animate-spin inline-block w-8 h-8 border-4 rounded-full"
          role="status"
        >
           <div className="loading-text absolute inset-0 flex items-center justify-center"></div>
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    </div>
  );
};

export default Loading;
