import { useState, useEffect, useRef } from "react";
import { Dialog } from "@headlessui/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import { motion, useAnimation } from "framer-motion";
import { useInView } from 'react-intersection-observer';
import { Link } from 'react-router-dom';


const animationVariants = {
  hidden: { opacity: 0, y: '-100%' },
  visible: { opacity: 1, y: '0%', transition: { duration: 1.5, ease: "easeOut" } }
};

export default function Header({ currentSection, scrollToSection }) {
  const [scrolling, setScrolling] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const control = useAnimation();
  const { ref, inView } = useInView({
    triggerOnce: true, 
    threshold: 0, 
  });
  const isMounted = useRef(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollThreshold = window.innerHeight / 20;
      setScrolling(window.scrollY > scrollThreshold);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);
  
  useEffect(() => {
    isMounted.current = true;
    const handleLoading = async () => {
      await new Promise((resolve) => setTimeout(resolve, 1000));
      if (isMounted.current) {
        control.start("visible");
      }
    };

    handleLoading();

  return () => {
      isMounted.current = false;
    };
  }, [control]);

  const handleNavClick = (sectionId) => {
    scrollToSection(sectionId);
    setMobileMenuOpen(false);
  };
  
  return (
    <motion.header
    ref={ref}
    initial="hidden"
    animate={control}
    variants={animationVariants}
    className={`max-w-7xl mx-auto sticky top-1 z-10 ${scrolling ? "bg-cyan-700 bg-opacity-50 backdrop-blur-sm rounded-full" : "bg-transparent"}`}
  >
    
      <nav
        className="mx-auto flex max-w-7xl items-center justify-between"
        aria-label="Global"
      >
        <div className="flex lg:flex-1">
        <Link to="/#hero" onClick={() => handleNavClick('hero')} className="">
            <motion.div className={`link-with-fading-border h-auto max-h-16 w-auto ${
    currentSection === 0 ? "border-active" : ""
  }`}
              whileHover={{ scale: 1.2, rotate: 15 }}
              whileTap={{ scale: 0.8, rotate: -15, borderRadius: "100%" }}
             
            >
              <img className="transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-110 duration-300 h-16 sm:h-16 md:h-16 lg:h-20 w-auto"
                src="Ernestas Logo.png"
                alt="Ernestas Kriskus written in the logo"
              />
            </motion.div>
          </Link>
        </div>
        <div className="flex lg:hidden transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-110 duration-300 text-lg font-semibold leading-6">
          <button
            type="button"
            className="inline-flex items-center justify-center rounded-md p-3.5 text-orange-500"
            onClick={() => setMobileMenuOpen(true)}
          >
            <span className="sr-only"></span>
            <Bars3Icon className="h-8 w-8" aria-hidden="true" />
          </button>
        </div>
        <div className="hidden lg:flex lg:gap-x-12">
        {["About me", "Work Experience", "Education", "Projects"].map((text, idx) => (
            <Link key={text} to={`/#${text.toLowerCase().replace(' ', '')}`}
            onClick={() => handleNavClick(text.toLowerCase().replace(' ', ''))}
               className={`link-with-fading-border transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-110 duration-300 text-lg font-semibold leading-6 ${currentSection === idx + 1 ? "border-active" : ""}`}>
              {text}
            </Link>
          ))}
        </div>
        <div className=" hidden lg:flex lg:flex-1 lg:justify-end">
          <Link
              to="/#contactme"
              onClick={() => handleNavClick('contactme')}
            className={`link-with-fading-border transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-110 duration-300 text-lg font-semibold leading-6 ${
              currentSection === 5 ? "border-active" : ""
            }`}
          >
            Contact me<span aria-hidden="true">&rarr;</span>
          </Link>
        </div>
      </nav>
      <Dialog
        className="lg:hidden"
        open={mobileMenuOpen}
        onClose={setMobileMenuOpen}
      >
        <div className="fixed inset-0 z-10" />
        <Dialog.Panel className="fixed inset-y-0 right-0 z-10 w-full overflow-y-auto bg-white px-6 py-6 sm:max-w-sm sm:ring-1 sm:ring-gray-900/10">
          <div className="flex items-center justify-between">
            <Link
              to="/#hero"
              className="-m-1.5 p-1.5"
              onClick={() => setMobileMenuOpen(false)}
            >
              <span className="sr-only"></span>
              <motion.div
              whileHover={{ scale: 1.2, rotate: 15 }}
              whileTap={{ scale: 0.8, rotate: -15, borderRadius: "100%" }}
             
            >
              <img
                className="transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-300 duration-300 h-10 lg:h-20 w-auto"
                src="Ernestas Logo.png"
                alt="Ernestas Kriskus written in logo"
              />
               </motion.div>
            </Link>
            <motion.button
              type="button"
              className="-m-2.5 rounded-md p-2.5 text-gray-700 transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-110 duration-300 text-lg font-semibold leading-6"
              onClick={() => setMobileMenuOpen(false)}
            >
              <span className="sr-only"></span>
              <XMarkIcon className="h-6 w-6" aria-hidden="true" />
            </motion.button>
          </div>
          <div className="mt-6 flow-root">
            <div className="-my-6 divide-y divide-gray-500/10">
              {["About me", "Work Experience", "Education", "Projects", "Contact Me"].map((text) => (
                
                <Link key={text} to={`/#${text.toLowerCase().replace(' ', '')}`} 
                
                   className="transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-300 duration-300 -mx-3 block rounded-lg px-3 py-2 text-base font-semibold leading-7 text-gray-900 hover:bg-gray-50"
                   onClick={() => handleNavClick(text.toLowerCase().replace(' ', ''))}>
                  {text}
                </Link>
              ))}
            </div>
          </div>
        </Dialog.Panel>
      </Dialog>
    </motion.header>
  );
}
