import "D:/Ernestas/tuscias/react-portfolio/src/App.css";
import { useState, useEffect } from "react";
import Header from "./components/Header";
import Hero from "./components/Hero";
import AboutMe from "./components/AboutMe";
import WorkExperience from "./components/WorkExperience";
import Education from "./components/Education";
import MouseTracker from "./components/MouseTracker";
import Projects from "./components/Projects";
import Footer from "./components/Footer";
import EmploymentVerificationProject from "./components/EmploymentVerificationProject/EmploymentVerificationProject";
import { HashRouter as Router, Route, Routes } from "react-router-dom";
import AboutUsEmployment from './components/EmploymentVerificationProject/AboutUsEmployment.jsx';
import RequestForm from "./components/EmploymentVerificationProject/RequestForm.jsx";
import GetHired from "./components/EmploymentVerificationProject/GetHired.jsx";
import Login from "./components/EmploymentVerificationProject/Login.jsx";
import EmployeeInformation from "./components/EmploymentVerificationProject/EmployeeInformation.jsx";
import { UserProvider } from './components/EmploymentVerificationProject/UserContext';
import Register from "./components/EmploymentVerificationProject/Register.jsx";
import SmoothScroll from "./components/SmoothScroll";




function MainContent() {
  const [currentSection, setCurrentSection] = useState(0);

  useEffect(() => {
    const sectionIds = ['hero', 'aboutme', 'workexperience', 'education', 'projects', 'contactme'];
    const observers = [];

    sectionIds.forEach((id, index) => {
      const element = document.getElementById(id);
      if (element) {
        const observer = new IntersectionObserver(
          ([entry]) => {
            if (entry.isIntersecting) {
              setCurrentSection(index);
            }
          },
          { threshold: 0.5 }
        );
        observer.observe(element);
        observers.push(observer);
      }
    });

    return () => {
      observers.forEach(observer => observer.disconnect());
    };
  }, []);

 const scrollToSection = (sectionId) => {
  if (sectionId === 'hero') {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  } else {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  }
};
  return (
    <UserProvider>
   
    <div className="App">
     
      <header className="App-header">
     
        <MouseTracker />
        <SmoothScroll>
        <Routes>
          <Route path="/" element={
            <>
              <Header currentSection={currentSection} scrollToSection={scrollToSection}/>
              <Hero currentSection={currentSection} />
              <AboutMe currentSection={currentSection} />
              <WorkExperience currentSection={currentSection} />
              <Education currentSection={currentSection} />
              <Projects currentSection={currentSection} />
              <Footer currentSection={currentSection} />
            </>
          } />
          <Route path="/project/EmploymentVerificationProject" element={<EmploymentVerificationProject />} />
          <Route path="/project/EmploymentVerificationProject/aboutUs/read" element={<AboutUsEmployment />} />
          <Route path="/project/EmploymentVerificationProject/requestForm/create" element={<RequestForm />} />
          <Route path="/project/EmploymentVerificationProject/employeeInfo/read" element={<EmployeeInformation />} />
          <Route path="/project/EmploymentVerificationProject/getHired/read" element={<GetHired />} />
          <Route path="/project/EmploymentVerificationProject/register/create" element={<Register />} />
          <Route path="/login" element={<Login />} />
        </Routes>
        </SmoothScroll>
      </header>
    </div>
  
  </UserProvider>
  );
}

function App() {
  return (
    <UserProvider>
      <Router>
        <div className="App">
           
          <header className="App-header">
            <SmoothScroll>
            <MainContent />
            </SmoothScroll>
          </header>
        </div>
      </Router>
    </UserProvider>
  );
}

export default App;
