import React, { useState } from 'react';
import "D:/Ernestas/tuscias/react-portfolio/src/style.css"
import { useNavigate } from 'react-router-dom';
import { Navigation, Footer, Head } from "./Fragments";

const Register = () => {
    const [formData, setFormData] = useState({
      emailAddress: '',
      password: '',
      name: '',
      address: '',
      phoneNumber: '',
      faxNumber: '',
      dotNumber: '',
    });
  
    const [errors, setErrors] = useState({});
  
    const handleChange = (e) => {
      setFormData({
        ...formData,
        [e.target.id]: e.target.value,
      });
    };
  
    const validate = () => {
      const newErrors = {};
      if (!formData.emailAddress.trim()) {
        newErrors.emailAddress = 'Email cannot be blank';
      }
      if (!formData.password.trim()) {
        newErrors.password = 'Password cannot be blank';
      }
      if (!formData.name.trim()) {
        newErrors.name = 'Please provide a name';
      }
      if (!formData.address.trim()) {
        newErrors.address = "Address can't be blank";
      }
      if (!formData.phoneNumber.trim()) {
        newErrors.phoneNumber = 'Please provide a phone number';
      }
      if (!formData.dotNumber.trim()) {
        newErrors.dotNumber = "DOT number can't be empty";
      }
      setErrors(newErrors);
      return Object.keys(newErrors).length === 0;
    };
  
    const navigate = useNavigate();
  
    const handleSubmit = (e) => {
      e.preventDefault();
      if (validate()) {
        navigate('/login');
      }
    };
  
    return (
      <div className="body-of-project flex flex-col min-h-screen ">
      <Navigation />
      <main className="grow container mx-auto flex flex-col justify-center items-center rounded-md min-h-screen w-full my-80">
        <form className="register-form grid grid-cols-2 gap-x-8 gap-y-10 p-10 rounded w-full max-w-xl md:max-w-7xl space-y-4 " onSubmit={handleSubmit}>
          <strong className="text-center col-span-2 text-3xl"><h3 >Please fill out the fields</h3></strong>
          <div className='flex flex-col '>
            <label htmlFor="emailAddress" className="form-label text-lg">Email Address For Login</label>
            <input
            id='emailAddress'
              type="email"
              className="form-control text-black rounded p-1"
              value={formData.emailAddress}
              onChange={handleChange}
            />
            {errors.emailAddress && <strong className="text-red-600">{errors.emailAddress}</strong>}
          </div>
          <div className='flex flex-col'>
            <label htmlFor="password" className="form-label text-lg">Password</label>
            <input
            id='password'
              type="password"
              className="form-control text-black rounded p-1"
              value={formData.password}
              onChange={handleChange}
            />
            {errors.password && <strong className="text-red-600">{errors.password}</strong>}
          </div>
          <div className='flex flex-col'>
            <label htmlFor="name" className="form-label text-lg">Company Name</label>
            <input
            id='name'
             type="text"
              className="form-control text-black rounded p-1"
              value={formData.name}
              onChange={handleChange}
            />
            {errors.name && <strong className="text-red-600">{errors.name}</strong>}
          </div>
          <div className='flex flex-col'>
            <label htmlFor="address" className="form-label text-lg">Company Address</label>
            <input
id='address'
              type="text"
              className="form-control text-black rounded p-1"
              value={formData.address}
              onChange={handleChange}
            />
            {errors.address && <strong className="text-red-600">{errors.address}</strong>}
          </div>
          <div className='flex flex-col'>
            <label htmlFor="phoneNumber" className="form-label text-lg">Phone Number</label>
            <input
            id='phoneNumber'
              type="text"
              className="form-control text-black rounded p-1"
              value={formData.phoneNumber}
              onChange={handleChange}
            />
            {errors.phoneNumber && <strong className="text-red-600">{errors.phoneNumber}</strong>}
          </div>
          <div className='flex flex-col'>
            <label htmlFor="faxNumber" className="form-label text-lg">Fax Number</label>
            <input
            id='faxNumber'
              type="text"
              className="form-control text-black rounded p-1"
              value={formData.faxNumber}
              onChange={handleChange}
            />
          </div>
          <div className='flex flex-col'>
            <label htmlFor="dotNumber" className="form-label text-lg">DOT Number</label>
            <input
            id='dotNumber'
              type="text"
              className="form-control text-black rounded p-1"
              value={formData.dotNumber}
              onChange={handleChange}
            />
            {errors.dotNumber && <strong className="text-red-600">{errors.dotNumber}</strong>}
          </div>
          <div className="flex justify-center col-span-2 mt-4">
            <button className="px-4 py-2 font-bold text-slate-200 bg-blue-600 rounded-lg hover:bg-blue-700 no-underline" type="submit">Submit the information</button>
          </div>
        </form>
      </main>
      <Footer />
    </div>
  );
};

export default Register;
