import React, { useState, useEffect } from "react";
import { motion, useAnimation } from "framer-motion";
import Loading from "./Loading";
import { Link } from 'react-router-dom';
import AnimatedText from "./AnimatedText";
import ScrollSection from "./ScrollSection";

function Hero() {
  const contentVariants = {
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { 
        staggerChildren: 0.2,
        when: "beforeChildren"
      }
    },
    hidden: { 
      opacity: 0, 
      y: 50,
      transition: { 
        staggerChildren: 0.05,
        when: "afterChildren"
      }
    }
  };
  const itemVariants = {
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { type: "spring", damping: 8, stiffness: 200 }
    },
    hidden: { 
      opacity: 0, 
      y: 20,
      transition: { type: "spring", damping: 8, stiffness: 200 }
    }
  };


  const [loading, setLoading] = useState(true);
  const control = useAnimation();

  useEffect(() => {
    const handleLoading = async () => {
      await new Promise((resolve) => setTimeout(resolve, 1000));
      setLoading(false);
      control.start("visible");
    };

    handleLoading();
  }, [control]);

  useEffect(() => {
    const handleMouseMove = (e) => {
      const { clientX, clientY } = e;
      const heroSection = document.querySelector("#hero");
      const moveX = (clientX / window.innerWidth - 0.5) * 10;
      const moveY = (clientY / window.innerHeight - 0.5) * 10;
      if (heroSection) {
        heroSection.style.backgroundPosition = `${50 + moveX}% ${50 + moveY}%`;
      }
    };

    window.addEventListener("mousemove", handleMouseMove);

    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
    };
  }, []);

  if (loading) {
    return <Loading />;
  }
  

  return (
    <ScrollSection >
    <motion.section id="hero"
      variants={contentVariants}
      className="max-w-7xl px-10 container min-h-screen flex flex-col items-center justify-center mx-auto"
    >
      <motion.div variants={itemVariants} className="text-center flex flex-col my-2">
        <h1 className="font-mono text-4xl sm:text-6x1 md:text-7x1 lg:text-7xl xl:text-8xl font-bold text-orange-200">
          <AnimatedText delay={0.4}>Java Back-End </AnimatedText><AnimatedText delay={1.5}>Web Developer </AnimatedText><AnimatedText delay={2.5}>Seeking New Challenges</AnimatedText>
        </h1>
      </motion.div>
      <Link
        to="/#contactme"
        className="custom-button transition ease-in-out delay-10 hover:-translate-y-0 hover:scale-110 duration-300 text-xl mt-6 flex flex-col justify-center py-6 px-12 rounded mb-16"
      >
        Contact me
      </Link>
      <div className="floating rounded-xl py-6 mb-16 w-full flex justify-center items-center flex-wrap lg:gap-x-3">
        <div className="mt-4 flex items-center justify-center flex-wrap space-x-2 md:space-x-4">
          <motion.img variants={itemVariants}
            src="java logo.svg"
            alt="Tool 2"
            className="custom-logo w-12 h-12 md:w-16 md:h-16 opacity-80 hover:opacity-100 relative"
          />
          <motion.img variants={itemVariants}
            src="javascript logo.svg"
            alt="Tool 2"
            className="custom-logo w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
          />
        
            <motion.img variants={itemVariants}
              src="Spring logo.svg"
              alt="Tool 1"
              className="custom-logo w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="react logo.svg"
              alt="Tool 1"
              className="custom-logo w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="html logo.svg"
              alt="Tool 2"
              className="custom-logo w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="css logo.svg"
              alt="Tool 2"
              className="custom-logo w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="bootstrap logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="tailwind logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="node logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="intellij logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="vscode logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="git logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="github logo hero.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="mysql logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="postgresql logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="docker logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="thymeleaf logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="Maven logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
            <motion.img variants={itemVariants}
              src="Postman logo.svg"
              alt="Tool 2"
              className="w-12 h-12 md:w-16 md:h-16 opacity-70 hover:opacity-100 relative"
            />
          
        </div>
      </div>
    </motion.section>
     </ScrollSection>
  );
}

export default Hero;
