import React from "react";
import { motion } from "framer-motion";
import ScrollSection from "./ScrollSection";

const contentVariants = {
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { 
      staggerChildren: 0.2,
      when: "beforeChildren"
    }
  },
  hidden: { 
    opacity: 0, 
    y: 50,
    transition: { 
      staggerChildren: 0.05,
      when: "afterChildren"
    }
  }
};

const itemVariants = {
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { type: "spring", damping: 8, stiffness: 200 }
  },
  hidden: { 
    opacity: 0, 
    y: 20,
    transition: { type: "spring", damping: 8, stiffness: 200 }
  }
};


function Education() {
  

  return (
    <ScrollSection id="education">
    <motion.section className="flex flex-col items-center justify-center max-w-7xl"
      variants={contentVariants}
    >
      <h2
        id="educationTitle"
        className="my-3 flex flex-row items-center text-5xl font-bold text-orange-200"
      >
        Education
      </h2>
      <motion.div variants={itemVariants} className="flex flex-col space-y-6"  >
        <motion.div variants={itemVariants}  className="flex flex-row items-start justify-between border-b-2 mx-6 text-orange-200">
            <h3 className="pt-6 text-2xl md:text-3xl font-bold ">
              Professional education in Java Programming (CodeAcademy)
            </h3>
          <h4 className="pt-6 text-2xl md:text-3xl font-bold">
            2023 - 2024
          </h4>
        </motion.div>

        <motion.ul variants={itemVariants}  className="mt-6 text-xl md:text-2xl text-orange-100 list-disc mx-6">
          <li>
            Completed an intense 6 month program focused on Java programming for
            employed individuals.
          </li>
          <li>
            Covered fundamental concepts of Java programming language,
            object-oriented programming principles, and application development.
          </li>
          <li>
            Completed hands on projects and assignments to reinforce learning
            and practical skills.
          </li>
          <li>
            Achieved certification in Java programming upon successful
            completion of the program.
          </li>
          <li>
            Acquired proficiency in Java programming, Spring Framework,
            Hibernate, JDBC, and other relevant technologies.
          </li>
          <li>
            Developed strong problem-solving, analytical, and critical thinking
            skills through practical exercises and real world application of
            Java programming concepts.
          </li>
        </motion.ul>
        <motion.div variants={itemVariants}  className="flex flex-col space-y-6">
          <div className="flex flex-row items-start justify-between border-b-2 mx-6 text-orange-200">
            <h3 className="text-2xl md:text-3xl font-bold">
              Bachelors degree, extended study in Physiotherapy (Lithuanian
              Sports University)
            </h3>
            <h4 className="text-2xl md:text-3xl font-bold">
              2018 - 2019
            </h4>
          </div>
        </motion.div>
        <motion.ul variants={itemVariants}  className="mt-6 text-xl md:text-2xl text-orange-100 list-disc mx-6">
          <li>
            Expanded my expertise in physiotherapy by delving into advanced
            treatment modalities and specialized techniques, equipping me with a
            deeper understanding of complex therapeutic interventions and
            innovative rehabilitation methods.
          </li>
        </motion.ul>
        <motion.div variants={itemVariants}className="flex flex-col space-y-6">
          <div  className="flex flex-row items-start justify-between border-b-2 mx-6 text-orange-200">
            <h3 className="text-2xl md:text-3xl font-bold ">
              Bachelors degree in Physiotherapy (Panevys College)
            </h3>
            <h4 className="text-2xl md:text-3xl font-bold ">
              2015 - 2018
            </h4>
          </div>
        </motion.div>
        <ul className="mt-6 text-xl md:text-2xl text-orange-100 list-disc mx-6">
          <motion.li variants={itemVariants} >
            During the studies I acquired foundational knowledge in the field
            and developed a lot of soft skills such as teamwork, problem
            solving, adaptability, attention to detail, time management,
            critical thinking.
          </motion.li>
        </ul>
      </motion.div>
    </motion.section>
    </ScrollSection>
  );
}

export default Education;
