import React, { useContext, useState } from "react";
import { Link } from "react-router-dom";
import { UserContext } from "./UserContext";
import "D:/Ernestas/tuscias/react-portfolio/src/style.css";

export const Navigation = () => {
  const { username } = useContext(UserContext);
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav id="navigation" className="flex justify-center w-full">
      <div
        id="navigation-header"
        className="flex flex-wrap justify-between items-center w-full max-w-7x1 px-4 py-2 mx-4 my-2"
      >
        <button
          id="navbar-toggler"
          className="block lg:hidden text-white"
          onClick={() => setIsOpen(!isOpen)}
        >
          <img
            className="h-8 w-8 md:h-12 md:w-24 fill-current"
            src="/burger menu.svg"
            alt="burger menu icon"
          />
        </button>
        <div
          className={`lg:flex lg:items-center ${
            isOpen ? "block" : "hidden"
          } w-full lg:w-auto`}
        >
          <ul
            id="left-side"
            className=" flex flex-col lg:flex-row items-center text-xl w-full lg:w-auto"
          >
            <li id="image" className="py-3 lg:py-0">
              <Link
                className="nav-link justify-center items-center"
                to="/project/EmploymentVerificationProject"
              >
                <img
                  className="lg:w-80 lg:h-24 w-40 h-12 border-x-4 border-y-4 rounded shadow-md"
                  src="/logo project 1.png"
                  alt="Logo"
                />
              </Link>
            </li>
            <li className="md:px-0 md:py-1 xl:px-0 xl:py-3">
              <Link
                className="px-2 py-1 xl:px-4 xl:py-3 border-2 border-transparent hover:border-white"
                to="/project/EmploymentVerificationProject/requestForm/create"
              >
                Employee Verification
              </Link>
            </li>
            <li className="md:px-0 md:py-1 xl:px-0 xl:py-3">
              <Link
                className="px-2 py-1 xl:px-4 xl:py-3 border-2 border-transparent hover:border-white"
                to="/project/EmploymentVerificationProject/aboutUs/read"
              >
                About Us
              </Link>
            </li>
            <li className="md:px-0 md:py-1 xl:px-0 xl:py-3">
              <Link
                className="px-2 py-1 xl:px-4 xl:py-3 border-2 border-transparent hover:border-white"
                to="/project/EmploymentVerificationProject/getHired/read"
              >
                Get Hired
              </Link>
            </li>
          </ul>
        </div>
        <div className=" ml-auto flex items-center">
          <div id="register-login-logout">
            <ul className="flex items-center text-lg lg:text-xl">
              <li>
                <Link
                  className="px-2 py-1 xl:px-4 xl:py-3 border-2 border-transparent hover:border-white"
                  to="/project/EmploymentVerificationProject/register/create"
                >
                  Register
                </Link>
              </li>
              <li>
                <Link
                  className="px-2 py-1 xl:px-4 xl:py-3 border-2 border-transparent hover:border-white"
                  to="/login"
                >
                  Login
                </Link>
              </li>
              <li className="flex items-center">
                <span id="username" className="px-4 py-3">
                  Welcome, {username ? username : "Guest"}!
                </span>
                <Link
                  className="px-2 py-1 xl:px-4 xl:py-3 border-2 border-transparent hover:border-white"
                  to="/login"
                >
                  Logout
                </Link>
              </li>
            </ul>
          </div>
          <div id="languageSelectorDiv" className="flex items-center ml-4">
            <select
              id="languageSelector"
              className="px-2 py-1 xl:px-4 xl:py-3 bg-white-700 text-black p-2 rounded text-lg"
            >
              <option className="disabled">Select Language</option>
              <option value="EN">English</option>
              <option value="ES">Espaola</option>
              <option value="LT">Lietuvi</option>
            </select>
          </div>
        </div>
      </div>
    </nav>
  );
};

export const Footer = () => (
  <footer className="flex justify-center w-full mt-auto ">
    <div id="footer" className="text-center mx-auto py-4 flex flex-col">
      <div id="header" className="text-lg font-bold">
        Contact
      </div>
      <div className="py-2">
        <h5 className="text-xl">Any questions? Get in contact</h5>
        <p>Employment verification project. All rights reserved.</p>
        <a
          href="mailto:test@test.test"
          className="bg-blue-600 text-slate-200 py-2 px-4 rounded-md mt-2 hover:bg-blue-700 hover:text-slate-200 inline-block"
        >
          Send an email
        </a>
      </div>
    </div>
  </footer>
);
