import React, { useState, useRef, useEffect } from 'react';
import Hammer from 'hammerjs';
import 'D:/Ernestas/tuscias/react-portfolio/src/App.css';
import EmploymentVerificationProject from './EmploymentVerificationProject/EmploymentVerificationProject';
import { useNavigate } from 'react-router-dom';

const Projects = () => {
  const [currentIndex, setCurrentIndex] = useState(0); 
  const [currentProject, setCurrentProject] = useState(null);
  const slideListRef = useRef(null);
  const navigate = useNavigate();

  const slides = [
    {title: 'EmploymentVerificationProject', thumbnail: './Thumbnail for aboutus.png', component: <EmploymentVerificationProject />},
  {title: 'Project 2', thumbnail: '', component: <></>},
  {title: 'Project 3', thumbnail: '', component: <></>},
  {title: 'Project 4', thumbnail: '', component: <></>},
  {title: 'Project 5', thumbnail: '', component: <></>}
  ];

  const next = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % slides.length);
  };

  const prev = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 1 + slides.length) % slides.length);
  };

  useEffect(() => {
    const slider = slideListRef.current;
    const swipe = new Hammer(slider);

    swipe.on("swipeleft", next);
    swipe.on("swiperight", prev);

    return () => {
      swipe.off("swipeleft", next);
      swipe.off("swiperight", prev);
    };
  }, []);

  const handleClick = (index) => {
    setCurrentIndex(index);
  };

  const handleOpenProject = (project) => {
    setCurrentProject(project);
  };

  const handleOpenProjectInNewTab = (project) => {
    window.open(`/#/project/${project.title}`, '_blank');
  };
  

  return (
    <div id='projects' className="relative h-screen min-h-screen">
      {!currentProject ? (
        <>
          <button className="transition ease-in-out delay-10 hover:-translate-y-10 hover:scale-110 duration-300 nav-arrow left-arrow" onClick={prev}>&lt;</button>
          <ul className="list relative w-full max-w-7xl h-full flex justify-center items-center" ref={slideListRef}>
            {slides.map((project, index) => (
              <li
                key={index}
                className={`${
                  index === currentIndex ? 'act center' :
                    index === (currentIndex - 1 + slides.length) % slides.length
                      ? 'prev'
                      : index === (currentIndex + 1) % slides.length
                        ? 'next'
                        : 'hide'
                } card`}
                onClick={() => handleClick(index)}
              >
               <img src={project.thumbnail} alt={project.title} className="w-full h-full object-cover rounded-lg" />
                {index === currentIndex && ( 
                  <span className="project-title text-4xl text-orange-200 absolute">{project.title}</span>
                )}
                {index === currentIndex && (
                  <>
                    <button
                      className="transition ease-in-out delay-10 hover:-translate-y-0 hover:scale-110 duration-300 custom-button-projects absolute bottom-10 p-2 rounded border-color"
                      onClick={() => handleOpenProjectInNewTab(project)}
                    >
                      Open in New Tab
                    </button>
                  </>
                )}
              </li>
            ))}
          </ul>
          <div className="slidable-indicator flex justify-center items-center absolute bottom-4 left-0 right-0">
            {slides.map((_, index) => (
              <div
                key={index}
                className={`pagination-circle ${index === currentIndex ? 'active' : ''}`}
                onClick={() => handleClick(index)}
              />
            ))}
          </div>
          <button className="transition ease-in-out delay-10 hover:-translate-y-10 hover:scale-110 duration-300 nav-arrow right-arrow" onClick={next}>&gt;</button>
        </>
      ) : (
        <>
          <button
            className="transition ease-in-out delay-10 hover:-translate-y-10 hover:scale-110 duration-300 nav-arrow left-arrow absolute top-4 left-4"
            onClick={() => setCurrentProject(null)}
          >
            &lt; Back
          </button>
          {currentProject.component}
        </>
      )}
    </div>
  );
};

export default Projects;
