import React, { useRef, useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';

const SmoothScroll = ({ children }) => {
  const scrollContainerRef = useRef(null);
  const [isLargeScreen, setIsLargeScreen] = useState(window.innerWidth >= 1024);
  const location = useLocation();

  useEffect(() => {
    const handleResize = () => {
      setIsLargeScreen(window.innerWidth >= 1024);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (!isLargeScreen) return;

    const scrollContainer = scrollContainerRef.current;

    const handleWheel = (e) => {
      e.preventDefault();
      const containerHeight = scrollContainer.clientHeight;
      const scrollAmount = e.deltaY > 0 ? containerHeight : -containerHeight;
      const targetScrollTop = Math.round(scrollContainer.scrollTop / containerHeight) * containerHeight + scrollAmount;
      
      scrollContainer.scrollTo({
        top: targetScrollTop,
        behavior: 'smooth'
      });
    };

    scrollContainer.addEventListener('wheel', handleWheel, { passive: false });

    return () => {
      scrollContainer.removeEventListener('wheel', handleWheel);
    };
  }, [isLargeScreen]);

  useEffect(() => {
    if (location.hash) {
      const id = location.hash.replace('#', '');
      const element = document.getElementById(id);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    }
  }, [location]);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <div 
      ref={scrollContainerRef} 
      style={{
        height: '100vh',
        overflowY: 'scroll',
        scrollSnapType: isLargeScreen ? 'y mandatory' : 'none'
      }}
    >
      {React.Children.map(children, child =>
        React.cloneElement(child, { scrollToSection })
      )}
    </div>
  );
};

export default SmoothScroll;
