import React, { useEffect, useRef, useState } from "react";
import { Navigation, Footer } from "./Fragments";
import { Link } from "react-router-dom";
import "D:/Ernestas/tuscias/react-portfolio/src/style.css";

const RequestForm = () => {
  const [fileMessage, setFileMessage] = useState("");
  const [form, setForm] = useState({
    file: "",
    dob: "",
    ssn: "",
    name: "",
    surname: "",
  });
  const [errors, setErrors] = useState({});
  const sections = useRef([]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm({ ...form, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          const link = document.querySelector(`a[href="#${entry.target.id}"]`);
          if (link) {
            if (entry.isIntersecting) {
              link.classList.add("text-blue-700");
            } else {
              link.classList.remove("text-blue-700");
            }
          }
        });
      },
      {
        root: null,
        rootMargin: "0px",
        threshold: 0.25,
      }
    );

    sections.current.forEach((section) => {
      if (section) {
        observer.observe(section);
      }
    });

    return () => {
      sections.current.forEach((section) => {
        if (section) {
          observer.unobserve(section);
        }
      });
    };
  }, []);

  return (
    <div className="body-of-project flex flex-col items-center min-h-screen">
      <Navigation />

      <div
        id="release-fields"
        className="gap-y-8 mx-0 grid flex-col justify-center items-start flex-grow my-80 rounded-md px-5 "
      >
        <main className="flex flex-col items-center flex-grow w-full max-w-7xl py-8">
        <div className="w-full max-w-2xl">
          {fileMessage && (
            <div className="alert alert-danger">
              <p>{fileMessage}</p>
            </div>
            
          )}
          </div>
          <form
            onSubmit={handleSubmit}
            className="flex flex-col items-center w-full max-w-lg p-4 space-y-4"
          >
            <div className="flex flex-col items-center w-full">
              <label htmlFor="formFileLg" className="form-label">
                A signed release form must be uploaded.(PDF) {fileMessage}
              </label>
              <input
                type="file"
                id="formFileLg"
                name="file"
                accept=".pdf"
                className="w-full p-2 border border-gray-300 rounded-lg"
                onChange={handleChange}
              />
              {errors.file && (
                <strong className="text-red-500">{errors.file}</strong>
              )}
            </div>
            <div className="w-full space-y-6">
              <div className="flex flex-col items-center">
                <label htmlFor="dob" className="form-label">
                  <strong>Employee DOB</strong>
                </label>
                <input
                  type="date"
                  id="dob"
                  name="dob"
                  value={form.dob}
                  min="1914-01-01"
                  max="2003-12-31"
                  required
                  className="w-full p-2 border border-gray-300 text-gray-500 rounded-lg"
                  onChange={handleChange}
                />
                {errors.dob && (
                  <strong className="text-red-500">{errors.dob}</strong>
                )}
              </div>
              <div className="flex flex-col items-center">
                <label htmlFor="ssn" className="form-label">
                  <strong>Employee SSN</strong>
                </label>
                <input
                  type="text"
                  id="ssn"
                  name="ssn"
                  placeholder="XXX-XX-XXXX"
                  value={form.ssn}
                  className="w-full p-2 border border-gray-300 text-gray-500 rounded-lg"
                  onChange={handleChange}
                />
                {errors.ssn && (
                  <strong className="text-red-500">{errors.ssn}</strong>
                )}
              </div>
              <div className="flex flex-col items-center">
                <label htmlFor="name" className="form-label">
                  <strong>Employee Name</strong>
                </label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  placeholder="Requested employee name"
                  value={form.name}
                  className="flex w-full p-2 border border-gray-300 text-gray-500 rounded-lg"
                  onChange={handleChange}
                />
                {errors.name && (
                  <strong className="text-red-500">{errors.name}</strong>
                )}
              </div>
              <div className="flex flex-col items-center">
                <label htmlFor="surname" className="form-label">
                  <strong>Employee Surname</strong>
                </label>
                <input
                  type="text"
                  id="surname"
                  name="surname"
                  placeholder="Requested employee surname"
                  value={form.surname}
                  className="mb-5 w-full p-2 border border-gray-300 text-gray-500 rounded-lg"
                  onChange={handleChange}
                />
                {errors.surname && (
                  <strong className="text-red-500">{errors.surname}</strong>
                )}
              </div>
            </div>
            <div id="policy-container" className="flex justify-center items-start w-full h-72 rounded-md">
              <div className="bg-white p-1 rounded-lg flex justify-start h-full">
                <div className="">
                  <div className="flex flex-col items-start space-y-2 font-bold text-lg relative border-gray-300 pb-1 ">
                    <a
                      href="#list-item-1"
                      className="text-blue-500 hover:underline no-underline border-b"
                    >
                      Consumer Reports Compliance
                    </a>
                    <a
                      href="#list-item-2"
                      className="text-blue-500 hover:underline no-underline border-b"
                    >
                      User Responsibilities
                    </a>
                    <a
                      href="#list-item-3"
                      className="text-blue-500 hover:underline no-underline border-b"
                    >
                      User Obligations
                    </a>
                    <a
                      href="#list-item-4"
                      className="text-blue-500 hover:underline no-underline border-b"
                    >
                      Data Protection Measures
                    </a>
                    <a
                      href="#list-item-5"
                      className="text-blue-500 hover:underline no-underline"
                    >
                      Acknowledgment
                    </a>
                  </div>
                </div>
              </div>
              <div className="w-4/5 h-full p-4 mb-4 overflow-y-auto rounded-lg">
                <div className="flex flex-col">
                  <h4
                    id="list-item-1"
                    className="font-bold"
                    ref={(el) => (sections.current[0] = el)}
                  >
                    Consumer Reports Compliance
                  </h4>
                  <p>
                    In providing access to the VOE Platform, Ernestas Trucking
                    Inc. may act as a conduit to receive consumer reports
                    ordered by the User from one or more consumer reporting
                    agencies. Ernestas Trucking Inc. will not review, alter,
                    assemble, or evaluate the information in such reports but
                    will merely deliver the same through the Platform.
                  </p>
                  <h4
                    id="list-item-2"
                    className="font-bold"
                    ref={(el) => (sections.current[1] = el)}
                  >
                    User Responsibilities
                  </h4>
                  <p>
                    User understands its legal responsibilities in connection
                    with the use of consumer reports. User agrees to comply with
                    applicable state and federal laws. User shall only obtain
                    and use reports for hiring purposes and shall not further
                    distribute them unless in compliance with the law.
                  </p>
                  <h4
                    id="list-item-3"
                    className="font-bold"
                    ref={(el) => (sections.current[2] = el)}
                  >
                    User Obligations
                  </h4>
                  <p>
                    User certifies compliance with relevant regulations to
                    consumer reporting agencies. User discloses the possibility
                    of obtaining a consumer report to the individual who is the
                    subject of the report. User obtains written consent from the
                    individual for obtaining the report. User provides a summary
                    of the individuals rights as required by applicable laws.
                    User will not utilize the report in violation of equal
                    employment opportunity laws. User advises individuals of
                    adverse employment actions in advance and provides necessary
                    information. User abides by the Drivers Privacy Protection
                    Act (DPPA) and related state laws.
                  </p>
                  <h4
                    id="list-item-4"
                    className="font-bold"
                    ref={(el) => (sections.current[3] = el)}
                  >
                    Data Protection Measures
                  </h4>
                  <p>
                    User limits dissemination of consumer information to those
                    with legitimate need and permissible purpose. User retains
                    and destroys consumer data securely. User protects the
                    privacy of consumer information in motor vehicle records.
                  </p>
                  <h4
                    id="list-item-5"
                    className="font-bold"
                    ref={(el) => (sections.current[4] = el)}
                  >
                    Acknowledgment
                  </h4>
                  <p>
                    User acknowledges receipt of relevant notices and
                    information related to consumer reports. By submitting this
                    form, the user acknowledges that they have thoroughly read
                    the policy and comprehensively understand the outlined
                    responsibilities mentioned above.
                  </p>
                </div>
              </div>
            </div>
            <Link
              type="submit"
              to="/project/EmploymentVerificationProject/employeeInfo/read"
              className="px-4 py-2 font-bold text-slate-200 bg-blue-600 rounded-lg hover:bg-blue-700 no-underline"
            >
              Submit the information
            </Link>
          </form>
        </main>
      </div>
      <Footer />
    </div>
  );
};

export default RequestForm;
