import React from "react";
import { motion } from "framer-motion";
import ScrollSection from "./ScrollSection";

function AboutMe() {

  const contentVariants = {
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { 
        staggerChildren: 0.2,
        when: "beforeChildren"
      }
    },
    hidden: { 
      opacity: 0, 
      y: 20,
      transition: { 
        staggerChildren: 0.05,
        when: "afterChildren"
      }
    }
  };

  const itemVariants = {
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { type: "spring", damping: 8, stiffness: 200 }
    },
    hidden: { 
      opacity: 0, 
      y: 20,
      transition: { type: "spring", damping: 8, stiffness: 200 }
    }
  };
  

  return (
    <ScrollSection id="aboutme">
    <motion.section
      className="flex items-center justify-center"
       variants={contentVariants}
       
    >
      <div className="flex-col sm:items-center min-h-screen lg:flex lg:items-center mx-8 sm:mx-8 md:mx-4 lg:mx-6 lg:flex-row  lg:space-x-6 w-full max-w-7xl">
        <div className="w-full lg:w-1/2 py-20 text-orange-100">
          <motion.div variants={itemVariants}>
            <h1 id="aboutTitle" className="rounded-lg flex flex-col items-start sm:flex sm:flex-col sm:items-center lg:flex lg:flex-row lg:items-start text-5xl font-bold text-orange-200">About Me</h1>
            <motion.p variants={itemVariants} className="mt-6 text-xl md:text-2xl">
              Recent graduate of Java development course seeking employment that
              will make best use of my skills and allow me to develop them
              further. I am a results-driven problem solver with a passion for
              full-stack Java development, specializing in backend systems using
              Spring Framework. Experienced in optimizing and scaling backend
              systems with SQL and NoSQL databases.
            </motion.p>
            <motion.p variants={itemVariants} className="text-xl md:text-2xl">
              Strong foundation in OOP principles and a commitment to writing
              clean, test-driven, efficient code. Skilled in frontend
              development equipped with basics of React and JavaScript. Proven
              ability to collaborate within multidisciplinary teams and
              effectively communicate complex technical concepts. Dedicated to
              continuous learning and staying updated with the latest
              technologies in the AI, machine learning, web and software
              development landscapes.
            </motion.p>
          </motion.div>
        </div>
        <motion.div variants={itemVariants} className="drop-shadow-2xl w-full lg:w-1/2">
          <img
            src="Photo of me.png"
            alt="Ernestas Kriskus"
            className="rounded-lg w-full h-auto opacity-70"
          />
        </motion.div>
      </div>
    </motion.section>
    </ScrollSection>
  );
}
export default AboutMe;
