import React, { useState, useContext } from 'react';
import "D:/Ernestas/tuscias/react-portfolio/src/style.css";
import { Link, useNavigate } from 'react-router-dom';
import { UserContext } from './UserContext';


const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const { setUsername } = useContext(UserContext);
  const navigate = useNavigate();

  const handleLogin = (e) => {
    e.preventDefault();
    if (!email || !password) {
      alert('Please enter both email and password');
      return;
    }
    if (email === 'admin@gmail.com' && password === 'admin') {
      setUsername(email);
      navigate('/project/EmploymentVerificationProject');
    } else {
      alert('Invalid email or password');
    }
  };


  return (
    <section className="flex items-center justify-center min-w-full min-h-screen bg-[#1D2E28] p-3 md:p-4 xl:p-5">
      <div className="container">
        <div className="flex justify-center h-fit">
          <div className="max-w-4xl w-full ">
            <div className="bg-white shadow-sm flex rounded-md overflow-hidden h-96">
              <div className="w-1/2 hidden md:block">
                <img
                  className="w-full h-full object-cover"
                  src="/login-image.jpg"
                  alt="Welcome back you've been missed!"
                />
              </div>
              <div className="w-full md:w-1/2 flex items-center justify-center">
                <div className="p-3 md:p-4 xl:p-5 w-full">
                  <div className="mb-5 text-center">
                    <h4>Welcome back you've been missed!</h4>
                  </div>
                  <div className="mb-5">
                    <button className="bg-slate-900 text-gray-100 rounded-3xl p-1 hover:h-10 duration-300 w-full flex items-center justify-center gap-2">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="16"
                        fill="currentColor"
                        className="bi bi-google"
                        viewBox="0 0 16 16"
                      >
                        <path d="M15.545 6.558a9.42 9.42 0 0 1 .139 1.626c0 2.434-.87 4.492-2.384 5.885h.002C11.978 15.292 10.158 16 8 16A8 8 0 1 1 8 0a7.689 7.689 0 0 1 5.352 2.082l-2.284 2.284A4.347 4.347 0 0 0 8 3.166c-2.087 0-3.86 1.408-4.492 3.304a4.792 4.792 0 0 0 0 3.063h.003c.635 1.893 2.405 3.301 4.492 3.301 1.078 0 2.004-.276 2.722-.764h-.003a3.702 3.702 0 0 0 1.599-2.431H8v-3.08h7.545z" />
                      </svg>
                      Log in with Google
                    </button>
                  </div>
                  <p className="text-center mt-4 mb-5">Or sign in with <strong>Email:</strong> admin@gmail.com <strong><br></br>Password:</strong> admin</p>
                  <p></p>
                  <form onSubmit={handleLogin}>
                    <div className="mb-3">
                      <strong><label htmlFor="email" className="form-label">
                        Email:
                      </label></strong>
                      <input
                        type="email"
                        className="form-control ml-8 border-gray-400 border-2 rounded-lg"
                        id="email"
                        placeholder="Email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                      />
                    </div>
                    <div className="mb-3">
                      <strong><label htmlFor="password" className="form-label">
                        Password:
                      </label></strong>
                      <input
                        type="password"
                        className="form-control ml-1 border-gray-400 border-2 rounded-lg"
                        id="password"
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                      />
                    </div>
                    <div className="mb-3 form-check">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        id="remember_me"
                      />
                      <label className="form-check-label text-secondary" htmlFor="remember_me">
                        Keep me logged in
                      </label>
                    </div>
                    <div>
                      <button className="bg-slate-900 text-gray-100 rounded-3xl p-1 hover:-translate-x-1 hover:scale-100 duration-300" type="submit">
                        Log in now
                      </button>
                    </div>
                  </form>
                  <div className="mt-5 text-center flex flex-row justify-around ">
                    <Link to="/requestor/create" className="bg-gray-400 text-gray-100 rounded-3xl p-1 hover:translate-x-1 hover:scale-100 duration-300">
                      Create new account
                    </Link>
                    <Link to="#!" className="bg-gray-400 text-gray-100 rounded-3xl p-1 hover:-translate-x-1 hover:scale-100 duration-300">
                      Forgot password
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Login;
