import React from "react";
import "D:/Ernestas/tuscias/react-portfolio/src/App.css";
import { Link } from 'react-router-dom';
import ScrollSection from "./ScrollSection";
import { motion } from "framer-motion";

const contentVariants = {
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { 
      staggerChildren: 0.2,
      when: "beforeChildren"
    }
  },
  hidden: { 
    opacity: 0, 
    y: 20,
    transition: { 
      staggerChildren: 0.05,
      when: "afterChildren"
    }
  }
};

const itemVariants = {
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { type: "spring", damping: 8, stiffness: 200 }
  },
  hidden: { 
    opacity: 0, 
    y: 20,
    transition: { type: "spring", damping: 8, stiffness: 200 }
  }
};

function Footer() {
  return (
    <ScrollSection >
    <motion.section id="contactme"
      variants={contentVariants}
      className="fading-border mx-auto flex flex-col items-center justify-start min-h-screen"
    >
      <motion.div variants={itemVariants} className="w-full flex justify-center">
        <Link to="#hero">
        <img
          id="logo"
          className="custom-button rounded-lg transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-100 duration-300 h-20 lg:h-20 w-auto my-16"
          src="Ernestas Logo.png"
          alt="Ernestas Kriskus written in logo"
        ></img>
        </Link>
      </motion.div>

      <motion.div variants={itemVariants} className="mb-16 max-w-7xl flex justify-center">
      <h1 className="text-2xl md:text-3xl text-center font-mono">Get in touch and let's work together for the brighter future</h1>
      </motion.div>
  <motion.div variants={itemVariants} className="border-2 border-color custom-button rounded-full py-6 mb-16 w-full flex justify-center lg:gap-x-3">
    <a rel="external" target="_blank" href="https://www.linkedin.com/in/ernestas-kriskus-095999244">
    <img className="w-12 transition ease-in-out delay-1 hover:-translate-y-1 hover:scale-110 duration-300" src="linkedin logo.svg"></img>
    </a>
    <a rel="external" target="_blank" href="https://github.com/ernestaskriskus">
    <img className="w-12 transition ease-in-out delay-1 hover:-translate-y-1 hover:scale-110 duration-300" src="github logo.svg"></img>
    </a>
    <a target="_blank" href="mailto:ernestas.kriskus@gmail.com">
    <img className="w-12 transition ease-in-out delay-1 hover:-translate-y-1 hover:scale-110 duration-300" src="email logo.svg"></img>
    </a>
  </motion.div>
      <motion.nav variants={itemVariants} className="fading-border flex justify-center max-w-7xl w-full">
        <div className="flex flex-wrap justify-center gap-x-4 gap-y-4 lg:gap-x-12 sm:gap-x-6 my-16">
          {["About me", "Work Experience", "Education", "Projects"].map(
            (text) => (
              <Link
                key={text}
                to={`/#${text.toLowerCase().replace(" ", "")}`}
                className="transition ease-in-out delay-10 hover:-translate-y-1 hover:scale-110 duration-300 text-xl font-semibold leading-6"
              >
                {text}
              </Link>
            )
          )}
        </div>
      </motion.nav>
      <motion.div variants={itemVariants} className="flex justify-between  fading-border max-w-7xl w-full ">
        <h2 className="my-16 ml-8">Copyright  2024 Ernestas Kriskus</h2>
        <h3 className="my-16 mr-8">Vilnius, Lithuania</h3>
      </motion.div>
    </motion.section>
    </ScrollSection>
  );
}

export default Footer;
