import React from "react";
import { motion } from "framer-motion";
import { useInView } from 'react-intersection-observer';
import ScrollSection from "./ScrollSection";

const contentVariants = {
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { 
      staggerChildren: 0.2,
      when: "beforeChildren"
    }
  },
  hidden: { 
    opacity: 0, 
    y: 50,
    transition: { 
      staggerChildren: 0.05,
      when: "afterChildren"
    }
  }
};

const itemVariants = {
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { type: "spring", damping: 12, stiffness: 200 }
  },
  hidden: { 
    opacity: 0, 
    y: 20,
    transition: { type: "spring", damping: 12, stiffness: 200 }
  }
};

function WorkExperience() {

  const { ref, inView } = useInView({
    triggerOnce: true, 
    threshold: 0.1, 
  });

  return (
    <ScrollSection id="workexperience">
    <motion.section 
    className="mx-auto flex flex-col items-center justify-center max-w-7xl min-h-screen" variants={contentVariants}
    >
      
        <motion.h2 id="workExperienceTitle" variants={itemVariants} duration={2} delay={0} yStart={20} className="lg:my-3 flex flex-row items-center text-5xl font-bold text-orange-100">Work Experience</motion.h2>
        <div className="flex flex-col space-y-6">
        <motion.div variants={itemVariants} duration={2} delay={0} yStart={20} className="flex flex-row items-start justify-between border-b-2 mx-6">
          <h3 className="pt-6 text-2xl md:text-3xl font-bold text-orange-200">Transport Manager in USA Market, UAB TURBO LT</h3>
        <h4 className="pt-6 text-2xl md:text-3xl  font-bold text-orange-200">2019.10 - Present</h4>
        
        </motion.div>
        <motion.ul variants={itemVariants} duration={2} delay={0.1} yStart={20} 
        className="mt-6 text-xl md:text-2xl text-orange-100 list-disc mx-6">
          <li>
            Continuously optimizing and enhancing existing systems and tools by
            integrating newly acquired expertise in Java and JavaScript leading
            to task automation and enhanced workflow efficiency.
          </li>
          <li>
            Identifying and implementing cost saving solutions for the company.
            For instance, securing a contract with a toll management company
            resulting in reduced costs and saved time. Additionally implementing
            contract with transportation system which saved 95 hours of driver
            time and 480 gallons of fuel over nine months.
          </li>
          <li>
            Competently handling various types of documents to ensure
            organizational compliance.
          </li>
          <li>
            Proficiently managing time, planning and creating schedules and
            routes for optimal workflow.
          </li>
          <li>
            Promoting collaboration within the team to achieve the best outcomes
            in the workspace.
          </li>
          <li>
            Conducting a comprehensive examination of a
            company's financial expenditures and presenting detailed reports to
            the management.
          </li>
          <li>
            Monitoring and documenting driver performance metrics for
            performance evaluation.
          </li>
          <li>
            Driver onboarding, employment history gathering to assess drivers
            compatibility with our company.
          </li>
          <li>
            Managing accidents, claims, and insurance matters to ensure
            operational safety and compliance.
          </li>
        </motion.ul>
      </div>
    </motion.section>
    </ScrollSection>
  );
}

export default WorkExperience;