import React from 'react';
import { motion } from 'framer-motion';

const AnimatedText = ({ children, delay = 0, className = '' }) => {
  const wordVariants = {
    hidden: { 
      opacity: 0, 
      y: 50,
      scaleY: 0.5
    },
    visible: { 
      opacity: 1, 
      y: 0,
      scaleY: 1,
      transition: {
        type: 'spring',
        damping: 10,
        stiffness: 100,
        delay: delay,
        duration: 0.8
      }
    }
  };

  return (
    <motion.span
      className={className}
      variants={wordVariants}
      initial="hidden"
      animate="visible"
    >
      {children}
    </motion.span>
  );
};

export default AnimatedText;